import React, { Component } from 'react';

function zeroPad(number){
    return ('0'+number).slice(-2);
}

class Test extends Component {
    constructor(props) {
        super(props);
        this.state ={};
    }

    getTime(){
        let date = new Date();
        let month = zeroPad(date.getUTCMonth()+1)
        let day = zeroPad(date.getUTCDate())
        let hour = zeroPad(date.getUTCHours())
        let minute = zeroPad(date.getUTCMinutes())
        let second= zeroPad(date.getUTCSeconds())
        let time = `${month}/${day} ${hour}:${minute}:${second}`;
        return time;
    }
    
    componentDidMount(){
        let ip = '?';
        let location = '?';

        fetch(`http://www.geoplugin.net/json.gp`).then((response)=>{
            return response.json()
        }).then((json)=>{
            ip = json.geoplugin_request;
            location = `${json.geoplugin_city}, ${json.geoplugin_region}, ${json.geoplugin_countryName}`;
        }).catch((err)=>{
            console.log("error caught")
        }).finally(()=>{
            this.setState({
                ip:ip,
                location:location
            })
        })
    }

	render() {

        return <div>
            <h1>
                This is a test.<br/>
                Your IP address is {this.state.ip}.<br/>
                You live in {this.state.location}.
                The time was UTC {this.getTime()}
                <br/>
                <br/>
            </h1>

            <h2>
                At UTC {this.getTime()}, {this.state.ip} visited from {this.state.location}
            </h2>
        </div>


        

    }
}

export default Test;
